import React, { useState, useEffect } from "react";
import "./weather.css";

function Weatherinfo(props) {
  const { weather } = props;

  const [date, setDate] = useState(new Date());

  useEffect(() => {
    var timerID = setInterval(() => tick(), 1000);
    return function cleanup() {
      clearInterval(timerID);
    };
  });

  function tick() {
    setDate(new Date());
  }

  const getTime = (timeStamp) => {
    return `${new Date(timeStamp * 1000).getHours()} : ${new Date(
      timeStamp * 1000
    ).getMinutes()}`;
  };

  const weatherimg = `https://upload.wikimedia.org/wikipedia/commons/thumb/6/68/Antu_weather-many-clouds.svg/512px-Antu_weather-many-clouds.svg.png`;

  const [weatherlogo, weatherUpdateLogo] = useState(weatherimg);

  useEffect(() => {
    const weatherupdate = `${weather?.weather[0]?.main}`;
    switch (weatherupdate) {
      case "Clouds":
        weatherUpdateLogo(
          "https://upload.wikimedia.org/wikipedia/commons/thumb/6/68/Antu_weather-many-clouds.svg/512px-Antu_weather-many-clouds.svg.png"
        );
        break;

      case "Haze":
        weatherUpdateLogo(
          "https://cdn0.iconfinder.com/data/icons/clouds-and-precipitation-colored/64/04_Sun_with_fog_mist_haze_smoke_weather_forecast-4096.png"
        );
        break;

      case "Rain":
        weatherUpdateLogo(
          "https://cdn3.iconfinder.com/data/icons/herror-gradient/340/rain_storm_lightning_weather_sky_danger_thunder_storm-4096.png"
        );
        break;

      case "Mist":
        weatherUpdateLogo(
          "https://cdn4.iconfinder.com/data/icons/the-weather-is-nice-today/64/weather_39-4096.png"
        );
        break;

      case "Sunny":
        weatherUpdateLogo(
          "https://cdn0.iconfinder.com/data/icons/summer-253/512/sun_summer_sunny_weather_summertime_warm_holidays_meteorology_nature-4096.png"
        );
        break;

      case "Storm":
        weatherUpdateLogo(
          "https://cdn2.iconfinder.com/data/icons/season-6/340/rain_autumn_day_rainy_person_umbrella_season_storm-4096.png"
        );
        break;

      default:
        break;
    }
  }, [weather]);

  const [imgbg, updateImg] = useState(
    "https://cdn.pixabay.com/photo/2019/08/30/12/54/landscape-4441297_960_720.jpg"
  );

  useEffect(() => {
    const hour = new Date().getHours();
    console.log(hour);
    if (hour > 6 && hour < 12) {
      updateImg(
        "https://images.pexels.com/photos/547782/pexels-photo-547782.jpeg?cs=srgb&dl=pexels-stefan-lorentz-547782.jpg&fm=jpg"
      );
    } else if (hour > 12 && hour <= 15) {
      updateImg(
        "https://images.pexels.com/photos/4464601/pexels-photo-4464601.jpeg?cs=srgb&dl=pexels-musa-orta%C3%A7-4464601.jpg&fm=jpg"
      );
    } else if (hour > 16 && hour < 20) {
      updateImg(
        "https://images.pexels.com/photos/34296/sunset-mood-sun-afterglow.jpg?cs=srgb&dl=pexels-pixabay-34296.jpg&fm=jpg"
      );
    } else if (hour >= 20 && hour <= 23) {
      updateImg(
        "https://images.pexels.com/photos/534757/pexels-photo-534757.jpeg?cs=srgb&dl=pexels-kai-pilger-534757.jpg&fm=jpg"
      );
    } else if (hour >= 0 && hour <= 5) {
      updateImg(
        "https://images.pexels.com/photos/534757/pexels-photo-534757.jpeg?cs=srgb&dl=pexels-kai-pilger-534757.jpg&fm=jpg"
      );
    }
  }, [imgbg]);

  return (
    <>
      <div className="wrap-weather">
        <div className="container">
          <div className="row">
            <div
              className="col-8"
              style={{
                backgroundImage: `URL(${imgbg})`,
              }}
            >
              <div className="weather-info">
                <p id="city">
                  {weather
                    ? `${weather?.name}, ${weather?.sys?.country}`
                    : `City , Country`}
                </p>
                <div className="temp-data-align">
                  <p id="weather-temp">
                    {weather
                      ? `${Math.floor(weather?.main?.temp_min - 273)}Â°`
                      : ` `}
                    <span>C</span>
                  </p>
                  <div id="weather-time">
                    <p>{date.toLocaleTimeString()}</p>

                    <p>{new Date().toDateString()}</p>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-4">
              <div className="weather-data">
                <div id="weather-img">
                  <img src={weatherlogo} alt="" />
                </div>
                <p className="weather-type">
                  <span className="weather-right">
                    {weather && `${weather?.weather[0]?.main}`}
                  </span>
                </p>
                <hr id="type-hr" />
                <div className="weather-align">
                  <span className="data">Humidity</span>
                  <img
                    src="https://cdn4.iconfinder.com/data/icons/weather-717/64/humidity-water-rain-weather-4096.png"
                    alt=""
                    srcset=""
                  />
                  <span className="data-1">
                    {weather && `${weather?.main?.humidity}`}
                  </span>
                  <div className="clear"></div>
                  <hr />
                </div>
                <div className="weather-align">
                  <span className="data">Wind Speed</span>
                  <img
                    src="https://cdn3.iconfinder.com/data/icons/unigrid-phantom-science-vol-1/60/003_021_wind_battery_eco_wind_generator_science_future-4096.png"
                    alt=""
                    srcset=""
                  />
                  <span className="data-1">
                    {weather && `${weather?.wind?.speed}`}
                  </span>
                  <div className="clear"></div>
                  <hr />
                </div>
                <div className="weather-align">
                  <span className="data">Pressure</span>
                  <img
                    src="https://cdn1.iconfinder.com/data/icons/power-and-energy-11/80/vector_816_21-4096.png"
                    alt=""
                    srcset=""
                  />
                  <span className="data-1">
                    {weather && `${weather?.main?.pressure}`}
                  </span>
                  <div className="clear"></div>
                  <hr />
                </div>
                <div className="weather-align">
                  <span className="data">Surise</span>
                  <img
                    src="https://cdn2.iconfinder.com/data/icons/scenes-and-landscapes-part-5/269/83-4096.png"
                    alt=""
                    srcset=""
                  />
                  <span className="data-1">
                    {weather && `${getTime(weather?.sys?.sunrise)} A.M`}
                  </span>
                  <div className="clear"></div>
                  <hr />
                </div>
                <div className="weather-align">
                  <span className="data">Suset</span>
                  <img
                    src="https://cdn4.iconfinder.com/data/icons/outdoors-3/462/yosemite-afternoon-4096.png"
                    alt=""
                    srcset=""
                  />
                  <span className="data-1">
                    {weather && `${getTime(weather?.sys?.sunset)} P.M`}
                  </span>
                  <div className="clear"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Weatherinfo;
